.modal-window {
    background-color: rgba(32, 33, 36, .6);
    height: 100vh;
    width: 100vw;    
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 255;
    left: 0;
    top: 0;
    transition: .2s;
    pointer-events: none;
    opacity: 0;
}

.modal-active {
    opacity: 1;
    pointer-events: all;
}

.modal-window-content {
    background-color: rgb(255, 255, 255);
    border-radius: 12px;
    padding: 20px;
    transform: scale(0.7);
    transition: all .4s;
}

.modal-content-active {
    transform: scale(1);
}